@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
@font-face {
    font-family: didot-italic;
    src: url(../../static/media/DidotLTStd-Italic.f0ec071a.otf);
}

@font-face {
    font-family: didot-regular;
    src: url("../../static/media/Didot Regular.251039e6.ttf");
}

body {
    margin: 0;
    font-family: didot-regular, sans-serif;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

.btn {
    border: none;
    cursor: pointer;
    transform: 0.3s ease;
}
.btn-black {
    background: black;
    color: white;
    padding: 8px 56px;
    border: 2px solid black;
    font-size: 1.1rem;
}
.btn-black:hover {
    background: transparent;
    color: black;
}
.btn-yellow {
    background-color: #f9d982;
    padding: 17px 20px;
    font-weight: 600;
    color: black;
}

.home-cta-section {
    display: flex;
}
.col-50 {
    flex: 1 1;
    display: flex;
}
.col-25 {
    flex: 1 1;
    display: flex;
}
.home-cta__btn,
.home-cta__btn:hover {
    width: 100%;
    color: white;
    text-decoration: none;
    transition: opacity 0.5s ease;
    padding: 0 3rem;
}
.home-cta__btn--one {
    background-color: #706c60;
}
.home-cta__btn--two {
    background-color: #baa798;
}
.home-cta__btn--three {
    background-color: #b9afa7;
}
.home-cta__btn--one:hover,
.home-cta__btn--two:hover,
.home-cta__btn--three:hover {
    opacity: 0.7;
}
/*  TEXT STYLING    */
.home-cta__title {
    font-size: 1.8rem;
    margin: 2.5rem 0 0 0;
    display: block;
}
.home-cta__subtitle {
    margin: 0 0 2.5rem 0;
    font-size: 0.9rem;
    font-weight: 100;
    display: block;
}
.cta-arrow {
    display: inline-block;
    margin-left: 0.5rem;
    transform: scale(0.9);
}

@media (max-width: 768px) {
    .home-cta__btn {
        padding: 0 2rem;
    }
    .home-cta__title {
        font-size: 1.25rem;
    }
}

@media (max-width: 576px) {
    .home-cta-section {
        flex-direction: column;
    }
    .home-cta__btn {
        padding: 0 1rem;
    }
    .home-cta__title {
        font-size: 1rem;
        margin-top: 1.5rem;
    }
    .home-cta__subtitle {
        margin-bottom: 1.5rem;
        font-weight: 300;
    }
}

.blog {
    padding: 1rem 3rem;
}
.blog__header {
    font-size: 1.45rem;
    text-transform: uppercase;
    padding-bottom: 10px;
    border-bottom: 1px solid lightgray;
    letter-spacing: 1px;
    margin-bottom: 0;
    font-family: didot-italic;
}

.blog__section {
    display: flex;
}
.blog__article {
    flex: 1 1;
    padding: 1rem 0;
    display: flex;
}
.blog__left img {
    transition: hover 0.3s ease;
    cursor: pointer;
}
.blog__left img:hover {
    opacity: 0.7;
}
.blog__right {
    padding-left: 1.25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.blog__title {
    font-size: 1.3rem;
    font-weight: 400;
}
.blog__desc {
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.7);
    line-height: 1.4;
}
.blog__btn {
    padding-bottom: 3px;
}
.blog__btn a {
    color: black;
    font-weight: 700;
}
.blog__cta {
    display: flex;
    justify-content: center;
    padding-top: 1.5rem;
}

@media (max-width: 768px) {
    .blog {
        padding: 0 2rem;
    }
    .blog__left {
        flex: 2 1;
    }
    .blog__left img {
        width: 100%;
    }
    .blog__right {
        flex: 4 1;
        padding-left: 1rem;
    }
}
@media (max-width: 576px) {
    .blog__section {
        flex-direction: column;
    }
    .blog {
        padding: 0 1rem;
    }
    .blog__header {
        font-size: 1.25rem;
    }
}

.search-section {
    padding: 1.5rem 3rem;
    display: flex;
    justify-content: center;
    font-family: "Roboto", sans-serif;
}
.search__field {
    border: 1px solid lightgray;
    border-right: none;
}

.search__field span {
    font-size: 0.9rem;
    padding: 15px 20px;
    padding-right: 10px;
}
.search__field input {
    border: none;
    outline: none;
    padding: 15px 20px;
    padding-left: 0;
    color: gray;
    font-size: 0.9rem;
    font-weight: 100;
    width: 250px;
}

@media (max-width: 768px) {
    .search__field input {
        width: 200px;
    }
}
@media (max-width: 576px) {
    .search-section {
        padding: 1.5rem 1rem;
    }
    .search-section {
        flex-direction: column;
    }
    .search__field {
        border-right: 1px solid lightgray;
    }
    .search__btn .btn-yellow {
        width: 100%;
    }
}



